/*

When a frontend wants to read information from a smart contract, it sends a request to the blockchain network containing the smart contract's address and the function name to be executed. 
The request is then broadcasted to all nodes in the network, which execute the specified function and return the result to the frontend.

When a frontend wants to write to a smart contract, it must send a transaction to the blockchain network containing the smart contract's address, the function name to be executed, and any necessary arguments. 
The transaction is then broadcasted to all nodes in the network and added to the blockchain only if it is valid. The write function in the smart contract updates the contract's state accordingly, 
and the result of the write function is stored on the blockchain for future reads.

Note that in both cases, the frontend must have the necessary permissions (e.g. an Ethereum account with sufficient funds to pay for gas) to interact with the smart contract.

*/