import { EthereumClient, modalConnectors } from "@web3modal/ethereum";
import { Web3Modal, Web3Button } from "@web3modal/react";
import { alchemyProvider } from "wagmi/providers/alchemy";
import { publicProvider } from "wagmi/providers/public";
import { goerli } from 'wagmi/chains'
import {
  configureChains,
  createClient,
  WagmiConfig,
  useAccount
} from "wagmi";
// import of modules and CSS
import { ReadContract, WriteContract } from "./ContractInteraction";
import "./styles.css";
import React, { useState, useEffect } from "react";

// initialize potential selectable chains and RPC provider (Alchemy)

const {provider} = configureChains(
  [goerli],
  [alchemyProvider({ apiKey: "p5USUMtxaphtpQ9WgLXmc0sxPbq26tbR" }), publicProvider()] // make alchemy account and create project with key, put in enb file for saftey
);

// add API Key
// Go on Wagmi and read how the connection work

// initialize Wagmi client
const wagmiClient = createClient({
  autoConnect: true,
  connectors: modalConnectors({ appName: "web3Modal", goerli }),
  provider
});

// Web3Modal Ethereum Client
const ethereumClient = new EthereumClient(wagmiClient, goerli); // we need another api key from web3modal

// module for walletconnect button
function HomePage() {
  return (
    <div className="wallet-btn">
      <Web3Button />
    </div>
  );
}

/* 


/* module to display if connection to wallett is sucessfull */

function Profile() {
  const { address, isConnected } = useAccount();

  if (isConnected) {
    return (
      <div>
        <br />
        <div className="centerDiv"> {`Your address in Web3: ${address}`}</div>
        <br />
      </div>
    );
  }

  if (!isConnected) {
    return (
      <div>
        <br />
        <div className="centerDiv">
          <h5> Not connected yet. </h5>
        </div>
      </div>
    );
  }
}



/*  module is exported and can be called in index.js;
    Web3Modal has an ID for connection;
    Web3 modules need to be embedded in </WagmiConfig>;
*/
export function WalletButton() {
  return (
    <>
      <WagmiConfig client={wagmiClient}>
        <HomePage />
        <Profile />
        <WriteContract />
        <br />
        <ReadContract />
      </WagmiConfig>

      <Web3Modal
        projectId=""
        theme="dark"
        accentColor="purple"
        ethereumClient={ethereumClient}
      />
    </>
  );
}
