import React, { useState } from "react";
import "./styles.css";
import { WalletButton } from "./WalletButton";
import {ReadContract, WriteContract} from "./ContractInteraction.js"
// import Web3 from "web3"; // to interact with smartContracts, alternative to stefans solution

export default function App() {
  const [ownerName, setOwnerName] = useState("");
  const [carModel, setCarModel] = useState("");
  const [insuranceNumber, setInsuranceNumber] = useState("");
  const [insuranceCompany, setInsuranceCompany] = useState("");
  const [insuranceType, setInsuranceType] = useState("");
  const [chargingAvailability, setChargingAvailability] = useState("");
  const [tokenID, setTokenID] = useState("");
  const [message, setMessage] = useState("");
  const [showImage, setShowImage] = React.useState(false);
  const [showAttributeUpdate, setAttributeUpdate] = React.useState(false);
  const [popupVisible, setPopupVisible] = useState(false);

  const handleFormSubmit = () => {
    // Save the data to variables that can be used to interact with a smart contract
    if (
      !ownerName ||
      !insuranceNumber ||
      !carModel ||
      !insuranceType ||
      !insuranceCompany
    ) {
      setMessage(
        "Your Digital Car ID can only be built if we have all the necessary information, please make a selection"
      );

      setOwnerName("")
      setInsuranceNumber("")
      setInsuranceCompany("")
      setInsuranceType("")
      setCarModel("")
      setShowImage(false);
      setAttributeUpdate(false);
      return;
    }
    // Save the data to variables that can be used to interact with a smart contract
    const data = {
      ownerName,
      carModel,
      insuranceCompany,
      insuranceType,
      insuranceNumber,
    };
    handleSubmit(data);
    
    setMessage(
      `Thank you! Your Information is saved successfully, your car is in the building process!`
    );
    
    // saving the value of the attributes only if all the values have been inputed by the user
    setOwnerName(ownerName)
    setInsuranceNumber(insuranceNumber)
    setInsuranceCompany(insuranceCompany)
    setInsuranceType(insuranceType)
    setCarModel(carModel)

    // showing the images and the attributes only after the click of the button
    setShowImage(true);
    setAttributeUpdate(true);
    setPopupVisible(!popupVisible);
    
    // adding page interaction to the message.
    if (!popupVisible) {
      const popup = document.querySelector(".popup");
      const popupRect = popup.getBoundingClientRect();
      const scrollPosition = popupRect.top + window.pageYOffset;
      window.scrollTo({ top: scrollPosition, behavior: "smooth" });
    }
  };

  // handleSubmit will call functions ReadContract and WriteContract 
  const handleSubmit = (data) => {
    ReadContract(data);
    WriteContract(data);
    console.log(data);
  
  };

  return (
    <div className="App">
      <header className="App-header">
        <WalletButton />
        <h1>Create a digital ID of your car</h1>
      </header>

      <header>
        <h2>Choose your attributes</h2>
      </header>
      <div className="form-container">
        <div className="formBox">
          <label>Owner's name:</label>
          <input
            type="text"
            className="text-input"
            value={ownerName}
            onChange={(e) => setOwnerName(e.target.value)}
          />
        </div>
      </div>
      <div className="form-container">
        <div className="formBox">
          <label>Insurance Number:</label>
          <input
            type="text"
            className="text-input"
            value={insuranceNumber}
            onChange={(e) => setInsuranceNumber(e.target.value)}
          />
        </div>
      </div>
      <div className="form-container">
        <div className="formBox">
          <label>Car Model:</label>
          <select
            value={carModel}
            className = "select"
            onChange={(e) => setCarModel(e.target.value)}
          >
            <option value=""></option>
            <option value="CMOption 1">BMW</option>
            <option value="CMOption 2">VW</option>
            <option value="CMOption 3">Toyota</option>
          </select>
        </div>
      </div>
      <div className="form-container">
        <div className="formBox">
          <label>Insurance Company:</label>
          <select
            value={insuranceCompany}
            className = "select"
            onChange={(e) => setInsuranceCompany(e.target.value)}
          >
            <option value=""></option>
            <option value="ICOption 1">Allianz</option>
            <option value="ICOption 2">ADAC</option>
            <option value="ICOption 3">Progressive</option>
          </select>
        </div>
      </div>
      <div className="form-container">
        <div className="formBox">
          <label>Insurance Type:</label>
          <select
            value={insuranceType}
            className = "select"
            onChange={(e) => setInsuranceType(e.target.value)}
          >
            <option value=""></option>
            <option value="ITOption 1">Liability Insurance</option>
            <option value="ITOption 2">Collision Insurance</option>
            <option value="ITOption 3">Comprehensive Insurance</option>
          </select>
        </div>
      </div>
      <button className="build-btn" onClick={handleFormSubmit}>
        {" "}
        Build my Car{" "}
      </button>

      {message && (
        <div className="popup">
          <div className="popup-content">
            <span className="popup-close" onClick={() => setMessage("")}>
              &times;
            </span>
            <p>{message}</p>
          </div>
        </div>
      )}
      {showImage && (
        <div className="image-container">
          <img src={`https://ipfs.io/ipfs/QmVnC2GkUu1X1Y9Sq9QaLaaomJVP29uMqajPk7aHxLdw32`} alt="Building Process" />
        </div>
      )}
      {showAttributeUpdate && (
        <div className="form-container">
        <div className="formBox">
          <label>Charging Availability:</label>
          <select
            value={chargingAvailability}
            className = "select"
            onChange={(e) => setChargingAvailability(e.target.value)}
          >
            <option value=""></option>
            <option value="CAOption 1">Yes</option>
            <option value="CAOption 2">No</option>
          </select>
        </div>
        <div className="formBox">
          <label>Please enter your Token ID:</label>
          <input
            type="text"
            className="text-input"
            value={tokenID}
            onChange={(e) => setTokenID(e.target.value)}
          />
        </div>
        </div>
        
        

      )}

    </div>
  );
}
